import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AscRatesSpecialComponent } from './asc-rates-special.component';

const routes: Routes = [
  {
    path: '',
    component: AscRatesSpecialComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AscRatesSpecialRoutingModule { }
