import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { AscRate } from '@models';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS } from '@shared';
import { AscRateSpecial } from 'app/core/models/asc-rate-special.model';
import { ValidateFieldService } from 'app/core/services/validate-field.service';

@Component({
  selector: 'app-asc-rates-special',
  templateUrl: './asc-rates-special.component.html',
  styleUrls: ['./asc-rates-special.component.scss']
})
export class AscRatesSpecialComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<AscRate>;
  displayedColumns: string[] = [
    'applicationType',
    'memberOffline',
    'memberOnline',
    'nonMemberOffline',
    'nonMemberOnline',
  ];

  ascRateSpecial = new AscRateSpecial();
  ascRateSpecialList = [];
  ascRateSpecialForm: FormGroup;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    public validateFieldService: ValidateFieldService,
    private sweetAlertService: SweetAlertService,
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.ascRatesSpecial).subscribe((res: any) => {
      this.ascRateSpecialList = res.responseData.data;
      this.dataSource = new MatTableDataSource<AscRate>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  getAscRateSpecial(): void {
    this.apiService.findById(ENDPOINTS.ascRatesSpecial, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.ascRateSpecial = res.responseData.data;
      }
    });
  }


  update(): void {
    this.apiService.update(ENDPOINTS.ascRatesSpecial, this.ascRateSpecial).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  setFormValidator(): void {
    this.ascRateSpecial.status = COMMON_FUNCTION.ACTIVE;
    this.ascRateSpecialForm = this.formBuilder.group({
      applicationType: [''],
      memberOffline: ['', Validators.required],
      memberOnline: [''],
      nonMemberOffline: ['', Validators.required],
      nonMemberOnline: [''],
    });
  }

  resetFormValidator(): void {
    this.ascRateSpecial = new AscRateSpecial();
    this.ascRateSpecialForm.markAsUntouched();
    this.ascRateSpecialForm.markAsPristine();
  }

}
